@import url(https://necolas.github.io/normalize.css/8.0.0/normalize.css);
@import url(https://fonts.googleapis.com/css?family=Slabo+27px);
html {
  box-sizing: border-box;
}

body {
  background-size: cover;
  font-family: Arial,Helvetica,sans-serif;
  background-image: url(/Simulador-IMC-GC/static/media/background.c48d2529.svg);
  background-repeat: no-repeat;
  height: 100vh;
}

input{
  border-radius: 12px;
  background-color: white;
  text-align: center;
  border-color: #0066ff;
}

.grafica{
  background-color: #f8f9fab8;
  border-radius: 10px;
  padding-top: 2rem;
  padding-bottom: 2rem;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.contenedor {
  max-width: 600px;
  margin: 0 auto;
}

.boton {
  background-color: #0066ff;
  font-size: 16px;
  width: 100%;
  padding: 15px;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 30px;
  border-color: #343a4021;
  border-width: thick;
}
.boton:hover {
  cursor: pointer;
  background-color: #001220 !important;
}
header {
  background-color: #343a40;
  padding: 10px;
  font-weight: bold;
  color: white;
}
header h1 {
  font-size: 2rem;
  margin:0;
  font-family: 'Slabo 27px', serif;
  text-align: center;
}
.contenedor-formulario {
  background-color: #212529;
  padding: 3rem;
  color: white;
  font-size: larger;
}

.random
{
  width: 193px;
  background-color: rgb(0 102 255);
  height: 40px;
  color: white;
  font-size: 20px;
  border-radius: 30px;
  border: none;
}

.campo{
  display: -webkit-flex;
  display: flex;
  margin-bottom: 1rem;
  -webkit-align-items: center;
          align-items: center;
}
.campo label {
  -webkit-flex: 0 0 200px;
          flex: 0 0 200px;
}
.campo input[type="radio"] {
  margin: 1rem;
}

.error {
  background-color: #FBE9E7;
  border: 1px solid #F4511E;
  padding: 10px;
  margin-bottom: 20px;
  text-align: center;
  color: #F4511E;
}
.correcto {
  background-color: rgb(211, 249, 187);
  border: 1px solid rgb(50, 167, 0);
  padding: 10px;
  margin-bottom: 20px;
  text-align: center;
  color: rgb(4, 150, 50);
}
.error p {
  margin:0;
}
.contenido {
  margin-top: 60px;
  background-color: white;
  box-shadow: 0px 21px 46px -15px rgba(0,0,0,1);
}
.resultado  {
  margin-bottom: 40px;
}
.resultado div {
  border: 1px solid #26C6DA;
  text-align: center;
  padding: 0  0 20px 0;
}
.resultado div p {
  margin-bottom: 0;
}
.resultado div p.header {
  background-color: #26C6DA;
  margin-bottom: 10px;
  color:white;
  padding: 10px;
  text-transform: uppercase;
  font-weight: bold;
}


span.resultado {
  position: relative;
  width: 100%;
}

.resultado-enter {
  transition: .5s;
  -webkit-transform: translateY(120%) ;
          transform: translateY(120%) ;
}

.resultado-enter.resultado-enter-active{
  -webkit-transform: translateY(120%);
          transform: translateY(120%);
}
.resultado-exit{
  -webkit-transform: translateY(0);
          transform: translateY(0);
  transition:.5s;
  position : absolute;
  left: 0;
  bottom : 0;
  width: 100%;
}
.resultado-exit-active{
  opacity: 0;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
    

.gran-total {
  font-size: 16px;
  border-radius: 30px;
  text-align: center;
  padding: .5rem;
  border: 1px solid #2be455;
  background-color: #00122054;
  margin-top: 1rem;
  font-weight: bold;
}
.gran-total p {

}

.resumen {
  padding: 1rem;
  text-align: center;
  background-color: #00838F;
  color: white;
  margin-top: 1rem;
}
.resumen li {
  list-style: none;
}
.resumen h2 {
  margin:0 0 1rem 0;
}
